	"vlCA": "linern regrese",
	"dzFE": "Linern regrese je statistick metoda pouvan k nalezen vztahu mezi dvma promnnmi. Tyto promnn jsou obvykle oznaeny jako zvisl promnn a nezvisl promnn. Clem linern regrese je najt nejlep linern aproximaci zvisl promnn v zvislosti na nezvisl promnn. Linern regrese se pouv k predikci hodnoty zvisl promnn pro danou hodnotu nezvisl promnn, kdy mezi nimi existuje linern vztah. Tento vztah je reprezentovn pomoc rovnice linern regrese, kter popisuje, jak se hodnoty zvisl promnn mn v zvislosti na hodnotch nezvisl promnn. Linern regrese je asto pouvna v rznch oblastech, jako je ekonomie, sociologie, biologie, psychologie, inenrstv a dalch.\n\nMetoda umouje upesnit, pro jakou transformaci modelu (nap. linern, logaritmovanou) hledme koeficient determinace.",
