	"Jpe0": "Pokud argumenty specifikuj faktorovou promnnou (druh argument), rozlo se prvn argument (buto numerick vektor nebo prvn vektor v matici, pokud je prvnm argumentem matice) dle hodnot faktoru do nov matice. Pokud jsou argumenty bez faktoru (tedy druh argument je przdn), vezme vechny numerick vektory z prvnho argumentu 'vectors' (matice) a nsledn odstran dky s przdnmi hodnotami. V ppad, e nen zadn argument 'factor' a argument 'vectors' zrove obsahuje pouze jedin vektor nebo je sm numerickm vektorem, vyvol chybu (bez faktoru jsou teba alespo dva numerick vektory v prvnm argumentu). V dceinn matici jsou nsledn vektory oitny od przdnch hodnot (bunk), avak nezvisle na sob, tzn. e vstupn vektory metody nemus mt stejn poet len, jako je to bn u jinch metod (korelac, provch test apod.). Tato skutenost me bt v rozporu se statistikou velikosti vzorku (vlastnost sample u td VectorAnalysis i MatrixAnalysis), je proto vhodn zohledovat spe hodnotu n, ppadn df u vstupu.",
