	"U8io": "Vektorov funkce jsou statistick metody, kter jsou provdny nad instancemi vektor. Jednoduchm pkladem je funkce 'sum', kter sete vechny neprzdn hodnoty v (numerickm vektoru). Vektorovch metod je k dispozici celkem kolem 30, piem nkter jsou prost (jako nap. 'sum')., jin jsou buto vpoetn komplexnj (testy normality), maj komplexn vstup (nap. histogram) nebo vyaduj ke svmu vpotu upesnit parametry (argumenty). Metody lze vyvolvat dvma zpsoby, buto pmo volnm metody z instance vektoru - nap vector.sum() - nebo volnm pes obecnou metodu **analyze**, nap. *vector.analyze('sum').run()*. Vce pstup lze zvolit i v zadvn argument. Ty lze bu zadvat dle poad uvedenm v dokumentaci jako leny argument - nap. parties.pci('ODS', 0.95) - nebo pomoc jedinho objektu, kter pomoc svch vlastnosti pesn definuje hodnoty argumenty - nap. parties.pci({value: 'ODS', alpha: 0.95}). Voln metod pes obecnou funkci **analyze** je efektivn v nkolika ohledech, mj. proto, e umouje sekvenn provdn analzy (nap. ped vpotem validovat argumenty pomoc metody *with*) a tak kvli zpisu metadat do instance tdy VectorAnalysis, konkrtn vstupn a vstupn velikost vzorku (objekt *sample* a vlastnosti *raw* a *net*) a asov parametry (object *time* a vlastnosti *from* a *to*, ze kterch metoda *duration* pstupn z instance analytick tdy pot celkovou dobu zpracovn vsledk.",
